<template>
  <base-field-label-wrapper
    :text="label"
    :info-text="infoText"
    :label-for="label"
    :is-required="isRequired"
  >
    <v-menu
      ref="dateMenu"
      v-model="dateMenu"
      :close-on-content-click="false"
      transition="scale-transition"
      offset-y
      max-width="290px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
          class="body-2 font-weight-semi-bold"
          clear-icon="mdi-close-circle"
          persistent-hint
          outlined
          dense
          readonly
          :rules="$attrs.rules"
          v-model="computedDate"
          v-bind="attrs"
          v-on="on"
        />
      </template>
      <v-date-picker
        v-model="date"
        no-title
        v-bind="$attrs"
        v-on="$listeners"
        @input="dateMenu = false"
      />
    </v-menu>
  </base-field-label-wrapper>
</template>

<script>
import BaseFieldLabelWrapper from '@components/BaseFieldLabelWrapper';
import formatDate from '@/utils/helpers/formatDate';

export default {
  name: 'BaseFieldDatepicker',
  inheritAttrs: false,
  components: { BaseFieldLabelWrapper },
  props: {
    label: {
      type: String,
      default: '',
    },
    infoText: {
      type: String,
      default: '',
    },
    isRequired: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      date: this.$attrs.value,
      dateMenu: false,
    };
  },
  computed: {
    computedDate() {
      return formatDate(this.$attrs.value);
    },
  },
};
</script>
