/* eslint-disable-next-line eslint linebreak-style */
<template>
  <base-field-label-wrapper
    :text="label"
    :info-text="infoText"
    :label-for="label"
    :is-required="isRequired"
  >
    <v-autocomplete
      class="body-2 font-weight-semi-bold"
      dense
      outlined
      autocomplete="off"
      :menu-props="{
        offsetY: true,
        maxHeight: 170,
      }"
      :items="items"
      v-bind="$attrs"
      v-on="$listeners"
    >
      <template v-slot:message="{ message }">
        <base-field-error :message="message" />
      </template>
      <template
        v-if="infiniteScroll && items.length"
        v-slot:append-item
      >
        <div
          v-intersect="onIntersect"
          class="px-4"
        >
          Loading more items ...
        </div>
      </template>
    </v-autocomplete>
  </base-field-label-wrapper>
</template>

<script>
import BaseFieldLabelWrapper from '@components/BaseFieldLabelWrapper';
import BaseFieldError from '@components/BaseFieldError';

export default {
  name: 'BaseFieldAutocomplete',
  inheritAttrs: false,
  components: {
    BaseFieldLabelWrapper,
    BaseFieldError,
  },
  props: {
    label: {
      type: String,
      default: '',
    },
    infoText: {
      type: String,
      default: '',
    },
    infiniteScroll: {
      type: Boolean,
      default: false,
    },
    items: {
      type: Array,
      default: () => [],
    },
    isRequired: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    onIntersect(entries, observer, isIntersecting) {
      if (isIntersecting) {
        this.$emit('onIntersect');
      }
    },
  },
};
</script>
