<template>
  <v-btn
    color="primary"
    class="btn-save-order-fixed"
    v-scroll="showBtnSaveOrder"
    v-show="isShowBtnSaveOrder"
    depressed
    x-large
    bottom
    fixed
    right
    v-bind="$attrs"
    @click="onClick"
  >
    <slot>Save</slot>
  </v-btn>
</template>

<script>
export default {
  name: 'ButtonSave',
  props: {
    onClick: {
      type: Function,
      required: true,
    },
  },
  data: () => ({
    isShowBtnSaveOrder: false,
  }),
  methods: {
    showBtnSaveOrder(event) {
      const offset = window.pageYOffset || event.target.scrollTop || 0;
      this.isShowBtnSaveOrder = offset > 20;
    },
  },
};
</script>
