                            </th>
                            <th class="pa-1">
                              Model
                            </th>
                            <th class="pa-1">
                              Type*
                            </th>
                            <th class="pa-1">
                              Color
                            </th>
                            <th class="pa-1">
                              Plate
                            </th>
                            <th class="pa-1">
                              VIN
