} from 'ramda';
import { createNamespacedHelpers } from 'vuex';
import capitalize from '@utils/helpers/capitalize';
import { actions } from '@/store/dictionaries/constants';
import { actions as loadsActions, getters as loadGetters } from '@modules/loads/constants';
import { actions as dispatchersActions } from '@modules/dispatchers/constants';
import PhotoInspection from '@components/Loads/PhotoInspection/PhotoInspection.vue';
import ButtonSave from '@components/LoadEdit/ButtonSave';
import ButtonBack from '@components/LoadEdit/ButtonBack';
import Modal from '@components/Modal/Modal.vue';
import BaseFieldInput from '@components/BaseFieldInput/BaseFieldInput.vue';
import BaseFieldTextarea from '@components/BaseFieldTextarea/BaseFieldTextarea.vue';
import BaseFieldAutocomplete from '@components/BaseFieldAutocomplete';
import BaseFieldLabelWrapper from '@components/BaseFieldLabelWrapper/BaseFieldLabelWrapper.vue';
import BaseFieldDatepicker from '@components/BaseFieldDatepicker/BaseFieldDatepicker.vue';
import ScrollToTop from '@components/ScrollToTop/ScrollToTop.vue';
import PanelDropdown from '@components/Loads/PanelDropdown/PanelDropdown.vue';
import PageNotFoundContent from '@components/PageNotFoundContent';
import BaseLayout from '@components/BaseLayout';
import formatDate from '@utils/filters/formatDate';
import {
  minLength,
  required,
  decimal,
  alpha,
  phone,
  email,
} from '@utils/helpers/validationRules';
import toBase64 from '@utils/helpers/toBase64';
import * as driversConstants from '@modules/drivers/constants';
import * as constants from './constants';

const { mapActions, mapState } = createNamespacedHelpers('dictionaries');
const {
  mapActions: mapLoadsActions,
  mapGetters: mapLoadsGetters,
  mapState: mapLoadsState,
} = createNamespacedHelpers('loads');
const {
  mapActions: mapDriversActions,
  mapState: mapDriversState,
} = createNamespacedHelpers('drivers');
const {
  mapActions: mapDispatchersActions,
  mapState: mapDispatchersState,
} = createNamespacedHelpers('dispatchers');

export default {
  name: 'LoadEdit',
  components: {
    PanelDropdown,
    ScrollToTop,
    BaseFieldDatepicker,
    BaseFieldLabelWrapper,
    BaseFieldTextarea,
    BaseFieldInput,
    PhotoInspection,
    PageNotFoundContent,
    BaseLayout,
    BaseFieldAutocomplete,
    ButtonSave,
    ButtonBack,
    Modal,
  },
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  directives: {
    mask,
  },
  data() {
    return {
      constants,
      isDeleteConfirmationModalOpened: false,
      billTo: {
        pickupInfo: false,
        deliveryInfo: false,
        shipperInfo: false,
      },
      billToQueryValues: {
        pickupInfo: 'pickup_company',
        deliveryInfo: 'delivery_company',
        shipperInfo: 'shipper_company',
      },
      isButtonSaveLoading: [],
      customer: {
        name: 'American Premium Logistics LLC (prev. Sumeca Solutions Corporations)',
      },
      attachmentsForm: {
        valid: false,
        values: [],
      },
      vehiclesForm: {
        valid: false,
        values: [],
        initialValues: [],
      },
      expensesForm: {
        valid: false,
        values: [],
        initialValues: [],
      },
      generalInformationForm: {
        valid: false,
        values: {},
        rules: {
          loadIdRules: [required('Load ID')],
          statusRules: [required('Status')],
        },
      },
      pickUpInformationForm: {
        valid: false,
        values: {},
      },
      deliveryInformationForm: {
        valid: false,
        values: {},
      },
      paymentInformationForm: {
        valid: false,
        values: {},
      },
      invoiceInformationForm: {
        valid: false,
        values: {},
      },
      shipperCastomerInformationForm: {
        valid: false,
        values: {},
      },
      activities: [
        {
          activityEvents: 'Order was marked as paid',
          activityDate: 'June 3, 2020, 1:52 p.m.',
          activityAuthor: 'whiteflagtransportllc@gmail.com',
        },
        {
          activityEvents: 'BOL was emailed to ttseky@icloud.com',
          activityDate: 'June 2, 2020, 5:49 p.m.',
          activityAuthor: 'harmy17@gmail.com',
        },
        {
          activityEvents:
              'Mikhailo Harmatiy marked the order as delivered to destination',
          activityDate: 'June 1, 2020, 5:34 p.m.',
          activityAuthor: 'harmy17@gmail.com',
        },
        {
          activityEvents: 'BOL was emailed to ttseky@icloud.com',
          activityDate: 'May 29, 2020, 9:23 p.m.',
          activityAuthor: 'harmy17@gmail.com',
        },
      ],
      initialLoadData: null,
    };
  },
  methods: {
    minLength,
    required,
    decimal,
    alpha,
    phone,
    email,
    getKeyByValue(object, value) {
      return Object.keys(object).find((key) => object[key] === value);
    },
    handleBillToChange(checked, checkedKey) {
      this.billTo = Object.keys(this.billTo)
        .reduce((result, billToKey) => ({
          ...result,
          [billToKey]: billToKey === checkedKey ? checked : false,
        }), {});

      this.generalInformationForm.values.bill_to = this.billToQueryValue || 'shipper_company';
    },
    handleBaseInformationFormStatusChange(value) {
      if (value.toLowerCase() === this.$constants.LOAD_STATUSES.NEW.toLowerCase()) {
        this.generalInformationForm.values.driver = null;
      }
    },
    handlePaymentInput(erase) {
      this.paymentInformationForm.values[`${erase}_payment`] = 0;
    },
    handleLoadDeleteConfirmation() {
      this.isDeleteConfirmationModalOpened = true;
    },
    handleDeleteConfirmationClose() {
      this.isDeleteConfirmationModalOpened = false;
    },
    handleLoadDelete() {
      this.handleDeleteConfirmationClose();
      this.updateStatus(this.$constants.LOAD_STATUSES.DELETED);
    },
    humanFileSize(size) {
      const index = Math.floor(Math.log(size) / Math.log(1024));

      /* eslint-disable */
      return (size / Math.pow(1024, index)).toFixed(2) * 1 + ' ' + ['B', 'kB', 'MB', 'GB', 'TB'][index];
    },
    handleVehicleTypeChange(index, value) {
      const typeName = this.vehicles.data.find((type) => type.id === value).name;
      this.vehiclesForm.values[index].fields.type_name = typeName;
    },
    handleVehicleMakeChange(index, value) {
      const makeName = this.vehiclesForm.values[index].autocompletes.makes.data.find((make) => make.id === value).name;
      this.vehiclesForm.values[index].fields.model = null;
      this.vehiclesForm.values[index].fields.make_name = makeName;
    },
    handleVehicleModelChange(index, value) {
      const modelName = this.vehiclesForm.values[index].autocompletes.models.data.find((model) => model.id === value).name;
      this.vehiclesForm.values[index].fields.model_name = modelName;
    },
    handleMakesAutocompleteChange(search, index) {
      this.getLoadVehiclesMakes(search, index);
    },
    handleModelsAutocompleteChange(search, index) {
      this.getLoadVehiclesModels(search, index);
    },
    handleFileChange(file, index) {
      this.expensesForm.values[index].fields.receipt = file;
    },
    getFileInformation(index) {
      if (this.expensesForm.values[index].fields.receipt instanceof File) {
        return {
          type: this.constants.EXPENSE_FILE_INPUT_TYPES.FILE,
          value: this.expensesForm.values[index].fields.receipt.name,
        };
      }

      if (typeof this.expensesForm.values[index].fields.receipt === 'string') {
        return {
          type: this.constants.EXPENSE_FILE_INPUT_TYPES.LINK,
          value: this.expensesForm.values[index].fields.receipt,
        };
      }

      return {
        type: undefined,
        value: '',
      };
    },
    hasPhotos(inspections) {
      return inspections.find(({ delivery, pickup }) => delivery.photos.length || pickup.photos.length);
    },
    handleDeleteAttachment(id, index) {
      if (id) {
        this.deleteLoadAttachment({ loadId: this.id, id })
          .then(() => {
            this.attachmentsForm.values = [...this.attachmentsForm.values.slice(0, index), ...this.attachmentsForm.values.slice(index + 1)];
            this.$notificationSuccess('Attachment was deleted');
          }).catch(() => {
            this.$notificationError('Attachment wasn\'t deleted');
          });
      } else {
        this.attachmentsForm.values = [...this.attachmentsForm.values.slice(0, index), ...this.attachmentsForm.values.slice(index + 1)];
      }
    },
    hanldePriceInput(form, key) {
      const value = this[form].values[key];
      const isValid = this.decimal(null)(value);
      const hasDot = value.indexOf('.') !== -1;

      if (isValid === true && hasDot) {
        const charsAfterDot = value.length - value.indexOf('.') - 1;

        if (charsAfterDot > 2) {
          this[form].values[key] = value.slice(0, value.length - 1);
        }
      }

      return false;
    },
    handleVehicleDelete(id, index) {
      if (id) {
        this.deleteLoadVehicle({ loadId: this.id, id })
          .then(() => {
            this.vehiclesForm.values = [...this.vehiclesForm.values.slice(0, index), ...this.vehiclesForm.values.slice(index + 1)];
          }).catch(() => {
            this.$notificationError('Vehicle wasn\'t deleted');
          });
      } else {
        this.vehiclesForm.values = [...this.vehiclesForm.values.slice(0, index), ...this.vehiclesForm.values.slice(index + 1)];
      }
    },
    handleExpenseDelete(id, index) {
      if (id) {
        this.deleteLoadExpense({ loadId: this.id, id })
          .then(() => {
            this.expensesForm.values = [...this.expensesForm.values.slice(0, index), ...this.expensesForm.values.slice(index + 1)];
            this.expensesForm.initialValues = [...this.expensesForm.values.slice(0, index), ...this.expensesForm.values.slice(index + 1)];
          }).catch(() => {
            this.$notificationError('Expense wasn\'t deleted');
          });
      } else {
        this.expensesForm.values = [...this.expensesForm.values.slice(0, index), ...this.expensesForm.values.slice(index + 1)];
        this.expensesForm.initialValues = [...this.expensesForm.values.slice(0, index), ...this.expensesForm.values.slice(index + 1)];
      }
    },
    async submitEditForm() {
      const isVehiclesFormValid = this.$refs.vehiclesForm.validate();
      const isExpensesFormValid = this.$refs.expensesForm.validate();
      const isGeneralInformationFormValid = this.$refs.generalInformationForm.validate();
      const isPickUpInformationFormValid = this.$refs.pickUpInformationForm.validate();
      const isDeliveryInformationFormValid = this.$refs.deliveryInformationForm.validate();
      const isPaymentInformationFormValid = this.$refs.paymentInformationForm.validate();
      const isInvoiceInformationFormValid = this.$refs.invoiceInformationForm.validate();
      const isShipperCastomerInformationFormValid = this.$refs.shipperCastomerInformationForm.validate();
      const isAttachmentsFormValid = this.$refs.attachmentsForm.validate();

      if (
        !isVehiclesFormValid
        || !isExpensesFormValid
        || !isGeneralInformationFormValid
        || !isPickUpInformationFormValid
        || !isDeliveryInformationFormValid
        || !isPaymentInformationFormValid
        || !isInvoiceInformationFormValid
        || !isShipperCastomerInformationFormValid
        || !isAttachmentsFormValid
      ) {
        this.$notificationError('Incorrect load information. Please check your inputs.');
        return;
      }

      if (this.vehiclesForm.values.length) {
        const newVehicles = this.vehiclesForm.values.filter((vehicle) => vehicle.new)
          .map(({ fields }) => ({ ...fields }));

        const existingVehicles = this.vehiclesForm.values.filter((vehicle) => !vehicle.new)
          .map(({ fields }) => ({ ...fields }));

        const editedVehicles = difference(existingVehicles, this.vehiclesForm.initialValues.map(({ fields }) => ({ ...fields })));

        if (newVehicles.length) {
          this.isButtonSaveLoading.push(true);
          this.addLoadVehicles({ id: this.id, data: newVehicles })
            .then((response) => {
              this.isButtonSaveLoading.pop();
              this.vehiclesForm.values = this.vehiclesForm.values.map((vehicle) => (console.log(vehicle), {
                fields: {
                  ...vehicle.fields,
                  id: vehicle.new ? response.data.splice(0, 1)[0] : vehicle.fields.id,
                },
                autocompletes: { ...vehicle.autocompletes },
                new: false,
              }));
              this.$notificationSuccess('Load was updated with new vehicles');
            })
            .catch(() => {
              this.isButtonSaveLoading.pop();
              this.$notificationError('Loads was not updated with new vehicles');
            });
        }

        if (editedVehicles.length) {
          editedVehicles.forEach((vehicle) => {
            this.editLoadVehicle({ id: this.id, vehicleId: vehicle.id, data: vehicle })
              .then(() => {
                this.vehiclesForm.initialValues = this.vehiclesForm.values.map(
                  (initialVehicle) => ({
                    fields: { ...initialVehicle.fields },
                    autocompletes: { ...initialVehicle.autocompletes },
                    new: false,
                  }),
                );
                this.$notificationSuccess(`Load vehicle ${vehicle.make_name} ${vehicle.model_name} was updated`);
              })
              .catch(() => {
                this.$notificationError(`Load vehicle with id ${vehicle.make_name} ${vehicle.model_name} was not updated`);
              });
          });
        }
      }

      if (this.expensesForm.values.length) {
        const newExpenses = this.expensesForm.values.filter((expense) => expense.new)
          .map(({ fields }) => ({ ...fields }));

        const expenses = this.expensesForm.values.filter((expense) => !expense.new)
          .map(({ fields }) => ({ ...fields }));

        const editedExpenses = difference(expenses, this.expensesForm.initialValues.map(({ fields }) => ({ ...fields })));

        if (newExpenses.length) {
          const precessedExpensesFiles = await Promise.all(newExpenses.map(async (expense) => {
            let receipt;

            if (expense.receipt instanceof File) {
              receipt = await toBase64(expense.receipt);
            } else {
              receipt = expense.receipt;
            }

            return {
              ...expense,
              receipt,
            };
          }));
          this.isButtonSaveLoading.push(true);
          await this.addLoadExpenses({ id: this.id, data: precessedExpensesFiles })
            .then(() => {
              this.isButtonSaveLoading.pop();
              this.$notificationSuccess('Load was updated with expenses');

              this.getLoad({ id: this.id })
                .then(() => {
                  this.expensesForm.values = this.load.data.expenses.map((expense) => ({
                    fields: { ...expense },
                    new: false,
                  }));
                  this.expensesForm.initialValues = this.load.data.expenses.map((expense) => ({
                    fields: { ...expense },
                    new: false,
                  }));
                })
                .catch((error) => {
                  console.dir(error);
                });
            })
            .catch(() => {
              this.isButtonSaveLoading.pop();
              this.$notificationError('Loads was not updated with expenses');
            });
        }

        if (editedExpenses.length) {
          const precessedExpensesFiles = await Promise.all(editedExpenses.map(async (expense) => {
            let receipt;

            if (expense.receipt instanceof File) {
              receipt = await toBase64(expense.receipt);
            }

            return receipt ? {
              ...expense,
              receipt,
            } : omit(['receipt'], expense);
          }));

          editedExpenses.forEach((expense, index) => {
            this.editLoadExpense({ id: this.id, expenseId: expense.id, data: precessedExpensesFiles[index] })
              .then(() => {
                this.$notificationSuccess(`Load expense with name ${expense.type_name} was updated`);
                this.getLoad({ id: this.id })
                .then(() => {
                  this.expensesForm.values = this.load.data.expenses.map((expense) => ({
                    fields: { ...expense },
                    new: false,
                  }));
                  this.expensesForm.initialValues = this.load.data.expenses.map((expense) => ({
                    fields: { ...expense },
                    new: false,
                  }));
                })
                .catch((error) => {
                  console.dir(error);
                });
              })
              .catch((error) => {
                this.$notificationError(`Load expense with name ${expense.type_name} was not updated`);
                Object.keys(error.response.data).forEach((key) => {
                  error.response.data[key].forEach((message) => {
                    this.$notificationError(message);
                  });
                });
              });
          });
        }
      }

      const newAttachments = this.attachmentsForm.values.filter(({ id }) => !id);

      if (newAttachments.length) {
        this.isButtonSaveLoading.push(true);
        this.addLoadAttachments({ id: this.id, data: newAttachments })
          .then(({ data }) => {
            this.isButtonSaveLoading.pop(true);
            const existing = this.attachmentsForm.values.filter(({ id }) => id);
            const added = newAttachments.map((_, index) => ({
              id: data[index],
              name: newAttachments[index].name,
              size: newAttachments[index].size,
            }));
            this.attachmentsForm.values = [...existing, ...added];
            this.$notificationSuccess('Load attachments was added');
            this.getLoadAttachments({ id: this.id })
              .then(() => {
                this.attachmentsForm.values = this.load.attachments;
              })
          })
          .catch(() => {
            this.isButtonSaveLoading.pop(true);
            this.$notificationError('Load attachment was not added');
          });
      }

      this.updateData({
        ...this.generalInformationForm.values,
        ...this.pickUpInformationForm.values,
        ...this.deliveryInformationForm.values,
        ...this.paymentInformationForm.values,
        ...this.shipperCastomerInformationForm.values,
        invoice: { notes: this.invoiceInformationForm.values.notes },
      });
    },
    onAddAttachments(files) {
      this.attachmentsForm.values = [...this.attachmentsForm.values, ...files];
    },
    onAddRowVehiclesTable() {
      this.vehiclesForm.values.push({
        fields: {
          inop: false,
          year: null,
          make: null,
          model: null,
          type: null,
          color: null,
          plate: null,
          vin: null,
          lot: null,
          additional_info: null,
        },
        autocompletes: {
          makes: {
            data: [],
            search: '',
            value: null,
            prevSearch: '',
          },
          models: {
            data: [],
            search: '',
            value: null,
            prevSearch: '',
          },
        },
        new: true,
      });
    },
    onAddRowExpensesTable() {
      this.expensesForm.values.push({
        fields: {
          type: null,
          price: null,
          date: null,
          show_in_invoice: false,
          deduct_from_driver: false,
          receipt: null,
        },
        new: true,
      });
    },
    onGoToBookmark(id) {
      if (this.$el.querySelector(id)) {
        this.$vuetify.goTo(id, { offset: 20 })
          .then(() => this.$el.querySelector(id).classList.add('highlight-bookmark-block'))
          .then(
            setTimeout(() => this.$el.querySelector(id).classList.remove('highlight-bookmark-block'), 2000),
          );
      }
    },
    getLoadVehiclesMakes: debounce(
      function bounce(search = '', index) {
        if (this.vehiclesForm.values[index].autocompletes.makes.prevSearch !== search) {
          axios.get(`/vehicles/makes?name=${search || ''}`, { requestName: 'vehicle makes' })
            .then(({ data }) => {
              this.vehiclesForm.values[index].autocompletes.makes.data = data;
            }).then(() => {
              this.vehiclesForm.values[index].autocompletes.makes.prevSearch = search;
            });
        }
      },
      100,
    ),
    getLoadVehiclesModels: debounce(
      function bounce(search = '', index) {
        const makeId = this.vehiclesForm.values[index].fields.make;

        if ((this.vehiclesForm.values[index].autocompletes.models.prevSearch !== search || search === '') && makeId) {
          axios.get(`/vehicles/models?make_id=${makeId}&name=${search || ''}`, { requestName: 'vehicle models' })
            .then(({ data }) => {
              this.vehiclesForm.values[index].autocompletes.models.data = data;
            }).then(() => {
              this.vehiclesForm.values[index].autocompletes.models.prevSearch = search;
            });
        }
      },
      100,
    ),
    updateStatus(nextStatus) {
      this.updateLoadStatus({ loadId: this.id, nextStatus })
        .then(() => {
          const slagified = slagify(nextStatus, { lower: true }).replace('_', ' ');
          this.generalInformationForm.values.status = slagified.split(' ').map((word) => capitalize(word)).join(' ');
          this.$notificationSuccess('Load status was updated');
        })
        .catch(() => this.$notificationError('Load status wasn\'t updated'));
    },
    updateData(data) {
      const difference = Object.keys(data).filter((key) => {
        if (typeof data[key] === 'object' && this.load.data[key] === null && key === 'invoice') {
          return false;
        } else if (typeof data[key] === 'object' && key === 'invoice' && this.load.data[key] !== null) {
          return this.load.data.invoice.notes !== data.invoice.notes;
        } else {
          if (key === 'pickup_payment' || key === 'delivery_payment') {
            return data[key] !== this.initialLoadData[key];
          } else {
            return data[key] !== this.load.data[key]
          }
        }
      });
      if (difference.length) {
        const localData = {};
        difference.forEach((key) => {
          if (key === 'driver' && data.driver) {
            localData[key] = this.drivers.data.find(({ full_name }) => data.driver === full_name).id;
          } else if (key === 'payment_method_name') {
            localData['payment_method'] = this.paymentMethods.data.find(({ name }) => name === data.payment_method_name).id;
          } else if (key === 'dispatcher' && data.dispatcher) {
            localData[key] = this.dispatchers.data.find(({ name }) => data.dispatcher === name).id;
          } else {
            localData[key] = data[key];
          }
        });
        this.isButtonSaveLoading.push(true);
        this.updateLoadData({ id: this.id, data: localData })
        .then(() => {
          this.$notificationSuccess('Load data was updated');
          this.isButtonSaveLoading.pop();
          if (this.id !== data.load_id) {
            this.$router.push({ name: this.$constants.ROUTE_NAMES.LOAD_EDIT, params: { id: data.load_id }, });
          }
          this.getLoad({ id: this.id }).then(() => {
            this.generalInformationForm.values = {
              id: this.load.data.id,
              load_id: this.load.data.load_id,
              driver: this.load.data.driver,
              driver_instructions: this.load.data.driver_instructions,
              dispatcher: this.load.data.dispatcher,
              status: this.load.data.status,
            };
            this.pickUpInformationForm.values = {
              pickup_company_name: this.load.data.pickup_company_name,
              pickup_address: this.load.data.pickup_address,
              pickup_city: this.load.data.pickup_city,
              pickup_state: this.load.data.pickup_state,
              pickup_zip: this.load.data.pickup_zip,
              pickup_contact_name: this.load.data.pickup_contact_name,
              pickup_phone: this.load.data.pickup_phone,
              pickup_email: this.load.data.pickup_email,
              scheduled_pickup_date: this.load.data.scheduled_pickup_date,
              buyer_number: this.load.data.buyer_number,
              // pickup_notes: this.load.data.pickup_notes,
              enclosed: this.load.data.enclosed,
            };
            this.deliveryInformationForm.values = {
              delivery_company_name: this.load.data.delivery_company_name,
              delivery_address: this.load.data.delivery_address,
              delivery_city: this.load.data.delivery_city,
              delivery_state: this.load.data.delivery_state,
              delivery_zip: this.load.data.delivery_zip,
              delivery_contact_name: this.load.data.delivery_contact_name,
              delivery_phone: this.load.data.delivery_phone,
              delivery_email: this.load.data.delivery_email,
              scheduled_delivery_date: this.load.data.scheduled_delivery_date,
              // delivery_notes: this.load.data.delivery_notes,
            };
            this.shipperCastomerInformationForm.values = {
              customer_company_name: this.load.data.customer_company_name,
              customer_address: this.load.data.customer_address,
              customer_city: this.load.data.customer_city,
              customer_state: this.load.data.customer_state,
              customer_zip: this.load.data.customer_zip,
              customer_contact_name: this.load.data.customer_contact_name,
              customer_phone: this.load.data.customer_phone,
              customer_email: this.load.data.customer_email,
              customer_fax: this.load.data.customer_fax,
            };
            this.paymentInformationForm.values = {
              payment_method_name: this.load.data.payment_method_name,
              carrier_fee: this.load.data.carrier_fee,
              broker_fee: this.load.data.broker_fee,
              delivery_payment: parseInt(this.load.data.delivery_payment) ? this.load.data.delivery_payment : 0,
              pickup_payment: parseInt(this.load.data.pickup_payment) ? this.load.data.pickup_payment : 0,
              payment_terms: this.load.data.payment_terms,
              payment_additional_info: this.load.data.payment_additional_info,
            };

            this.initialLoadData = {
              ...this.generalInformationForm.values,
              ...this.pickUpInformationForm.values,
              ...this.deliveryInformationForm.values,
              ...this.shipperCastomerInformationForm.values,
              ...this.paymentInformationForm.values,
            };
          });
        })
        .catch((error) => {
          if (error.response.status === 400) {
            this.$notificationError(error.response.data[0]);
          } else {
            this.$notificationError('Load data wasn\'t updated');
          }
          this.isButtonSaveLoading.pop();
        });
      }
    },
    ...mapActions({
      getStates: actions.GET_STATES,
      getVehicles: actions.GET_VEHICLES,
      getExpenses: actions.GET_EXPENSES,
      getPaymentMethods: actions.GET_PAYMENT_METHODS,
    }),
    ...mapDriversActions({
      getDrivers: driversConstants.actions.GET_DRIVERS,
    }),
    ...mapDispatchersActions({
      getDispatchers: dispatchersActions.GET_DISPATCHERS,
    }),
    ...mapLoadsActions({
      getLoad: loadsActions.GET_LOAD,
      getLoadAttachments: loadsActions.GET_LOAD_ATTACHMENTS,
      deleteLoadAttachment: loadsActions.DELETE_LOAD_ATTACHMENT,
      addLoadAttachments: loadsActions.ADD_LOAD_ATTACHMENTS,
      addLoadVehicles: loadsActions.ADD_LOAD_VEHICLES,
      editLoadVehicle: loadsActions.EDIT_LOAD_VEHICLE,
      deleteLoadVehicle: loadsActions.DELETE_LOAD_VEHICLE,
      addLoadExpenses: loadsActions.ADD_LOAD_EXPENSES,
      editLoadExpense: loadsActions.EDIT_LOAD_EXPENCE,
      deleteLoadExpense: loadsActions.DELETE_LOAD_EXPENSE,
      updateLoadStatus: loadsActions.UPDATE_LOAD_STATUS,
      updateLoadData: loadsActions.UPDATE_LOAD_DATA,
      getLoadVehicleInspections: loadsActions.GET_LOAD_VEHICLE_INSPECTIONS,
    }),
  },
  computed: {
    billToQueryValue() {
      return this.billToQueryValues[Object.keys(this.billTo).filter((key) => this.billTo[key])[0]] ?? undefined;
    },
    canSendAttachments() {
      return this.load.attachments.length > 0;
    },
    canSeePhotos() {
      return this.inspections.length && this.hasPhotos(this.inspections);
    },
    ...mapLoadsGetters({
      invoiceLink: loadGetters.INVOICE_LINK,
      receiptLink: loadGetters.RECEIPT_LINK,
      bolLink: loadGetters.BOL_LINK,
    }),
    ...mapState(['states', 'vehicles', 'expenses', 'paymentMethods']),
    ...mapLoadsState(['load', 'inspections']),
    ...mapDriversState(['drivers']),
    ...mapDispatchersState(['dispatchers']),
    computedLoadStatuses() {
      return Object.keys(this.$constants.LOAD_STATUSES).map((status) => {
        const slagified = slagify(status, { lower: true }).replace('_', ' ');
        return slagified.split(' ').map((word) => capitalize(word)).join(' ');
      });
    },
    isInitialValuesSet() {
      return !!(
        Object.keys(this.generalInformationForm.values).length
        && Object.keys(this.pickUpInformationForm.values).length
        && Object.keys(this.deliveryInformationForm.values).length
        && Object.keys(this.paymentInformationForm.values).length
        && Object.keys(this.invoiceInformationForm.values).length
        && Object.keys(this.shipperCastomerInformationForm.values).length
      );
    },
    actualPickupDate() {
      if (this.load && this.load.data.actual_pickup_date) {
        return `${formatDate(this.load.data.actual_pickup_date.split(' ')[0])} ${this.load.data.actual_pickup_date.split(' ')[1]}`;
      } else {
        return null;
      }
    },
    actualDeliveryDate() {
      if (this.load && this.load.data.actual_delivery_date) {
        return `${formatDate(this.load.data.actual_delivery_date.split(' ')[0])} ${this.load.data.actual_delivery_date.split(' ')[1]}`;
      } else {
        return null;
      }
    },
  },
  mounted() {
    this.getLoad({ id: this.id }).then(() => {
      this.generalInformationForm.values = {
        id: this.load.data.id,
        load_id: this.load.data.load_id,
        driver: this.load.data.driver,
        driver_instructions: this.load.data.driver_instructions,
        dispatcher: this.load.data.dispatcher,
        status: this.load.data.status,
        bill_to: this.load.data.bill_to,
      };
      this.pickUpInformationForm.values = {
        pickup_company_name: this.load.data.pickup_company_name,
        pickup_address: this.load.data.pickup_address,
        pickup_city: this.load.data.pickup_city,
        pickup_state: this.load.data.pickup_state,
        pickup_zip: this.load.data.pickup_zip,
        pickup_contact_name: this.load.data.pickup_contact_name,
        pickup_phone: this.load.data.pickup_phone,
        pickup_email: this.load.data.pickup_email,
        scheduled_pickup_date: this.load.data.scheduled_pickup_date,
        buyer_number: this.load.data.buyer_number,
        // pickup_notes: this.load.data.pickup_notes,
        enclosed: this.load.data.enclosed,
      };
      this.deliveryInformationForm.values = {
        delivery_company_name: this.load.data.delivery_company_name,
        delivery_address: this.load.data.delivery_address,
        delivery_city: this.load.data.delivery_city,
        delivery_state: this.load.data.delivery_state,
        delivery_zip: this.load.data.delivery_zip,
        delivery_contact_name: this.load.data.delivery_contact_name,
        delivery_phone: this.load.data.delivery_phone,
        delivery_email: this.load.data.delivery_email,
        scheduled_delivery_date: this.load.data.scheduled_delivery_date,
        // delivery_notes: this.load.data.delivery_notes,
      };
      this.shipperCastomerInformationForm.values = {
        customer_company_name: this.load.data.customer_company_name,
        customer_address: this.load.data.customer_address,
        customer_city: this.load.data.customer_city,
        customer_state: this.load.data.customer_state,
        customer_zip: this.load.data.customer_zip,
        customer_contact_name: this.load.data.customer_contact_name,
        customer_phone: this.load.data.customer_phone,
        customer_email: this.load.data.customer_email,
        customer_fax: this.load.data.customer_fax,
      };
      this.paymentInformationForm.values = {
        payment_method_name: this.load.data.payment_method_name,
        carrier_fee: this.load.data.carrier_fee,
        broker_fee: this.load.data.broker_fee,
        delivery_payment: parseInt(this.load.data.delivery_payment) ? this.load.data.delivery_payment : 0,
        pickup_payment: parseInt(this.load.data.pickup_payment) ? this.load.data.pickup_payment : 0,
        payment_terms: this.load.data.payment_terms,
        payment_additional_info: this.load.data.payment_additional_info,
      };
      this.initialLoadData = {
        ...this.generalInformationForm.values,
        ...this.pickUpInformationForm.values,
        ...this.deliveryInformationForm.values,
        ...this.shipperCastomerInformationForm.values,
        ...this.paymentInformationForm.values,
      };
      if (this.load.data.invoice) {
        this.invoiceInformationForm.values = {
          id: this.load.data.invoice.id,
          date: this.load.data.invoice.date.split(' ')[0],
          notes: this.load.data.invoice.notes,
        }
      } else {
        this.invoiceInformationForm.values = {
          id: '',
          date: '',
          notes: '',
        }
      }
      this.vehiclesForm.values = this.load.data.vehicles.map((vehicle) => ({
        fields: {
          ...vehicle,
        },
        autocompletes: {
          makes: {
            data: [{ name: vehicle.make_name, id: vehicle.make }],
            search: null,
            prevSearch: null,
          },
          models: {
            data: [{ name: vehicle.model_name, id: vehicle.model }],
            search: '',
            value: null,
            prevSearch: null,
          },
        },
        new: false,
      }));
      this.vehiclesForm.initialValues = this.load.data.vehicles.map((vehicle) => ({
        fields: {
          ...vehicle,
        },
        new: false,
      }));
      this.expensesForm.values = this.load.data.expenses.map((expense) => ({
        fields: { ...expense },
        new: false,
      }));
      this.expensesForm.initialValues = this.load.data.expenses.map((expense) => ({
        fields: { ...expense },
        new: false,
      }));
      this.getLoadAttachments({ id: this.id })
        .then(() => {
          this.attachmentsForm.values = this.load.attachments;
        });

      if (this.load.data.vehicles.length) {
        this.getLoadVehicleInspections({ loadId: this.id });
      }

      const billToKey = this.getKeyByValue(this.billToQueryValues, this.load.data.bill_to);
      this.billTo[billToKey] = true;

      this.getDrivers({ query: '?status=active,invited' });
      this.getDispatchers({ query: '?status=active' });
    });
    this.getStates();
    this.getVehicles();
    this.getExpenses();
    this.getPaymentMethods();
  },
};
</script>

