<template>
  <v-tooltip top>
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        class="btn-scroll-to-top"
        v-scroll="showBtnToTop"
        v-show="isShowBtnToTop"
        @click="onGoToTop"
        v-bind="attrs"
        v-on="on"
        fixed
        right
        bottom
        fab
        color="primary"
        x-small
        :ripple="false"
      >
        <span>
          <v-icon>mdi-arrow-up</v-icon>
        </span>
      </v-btn>
    </template>
    <span>Scroll to top</span>
  </v-tooltip>
</template>

<script>
export default {
  name: 'ScrollToTop',
  data: () => ({
    isShowBtnToTop: false,
  }),
  methods: {
    showBtnToTop(event) {
      if (typeof window === 'undefined') return;
      const offset = window.pageYOffset || event.target.scrollTop || 0;
      this.isShowBtnToTop = offset > 20;
    },
    onGoToTop() {
      this.$vuetify.goTo(0);
    },
  },
};
</script>

<style lang="scss" scoped>
.btn-scroll-to-top {
  bottom: 40px;
  right: 40px;
}

.btn-scroll-to-top,
.btn-scroll-to-top::v-deep.v-btn--fab.v-btn--contained:active {
  box-shadow: 0 0 0 12px rgba(39, 112, 239, 0.4), 0 0 0 24px rgba(39, 112, 239, 0.15);
}
</style>
