<template>
  <div>
    <div class="carousel">
      <span
        v-if="shouldShowControls"
        class="photos-counter text-body-2 primary-grey--text font-weight-bold btn-background-grey"
      >
        {{ activePhotoIndex + 1 }}/{{ photos.length }}
      </span>
      <span
        class="fullscreen btn-background-grey pa-2"
        @click="isModalOpened = true"
      >
        <v-img
          width="18"
          height="18"
          class="btn-background-grey px-2 py-1"
          src="@images/icons/inspection/fullscreen-gray.svg"
        />
      </span>
      <template v-if="$vuetify.breakpoint.mobile">
        <v-carousel
          v-resize="handlePageResize"
          key="carousel-mobile"
          ref="carousel"
          v-model="activePhotoIndex"
          :show-arrows="shouldShowControls && !$vuetify.breakpoint.mobile"
          :height="mobileCarouselHeight"
          hide-delimiters
          eager
          @change="handleActivePhotoIndexChange"
        >
          <v-carousel-item
            class="carousel-item"
            v-for="({ url: photo }) in photos"
            :transition="false"
            :key="photo"
            eager
          >
            <v-img
              ref="images"
              :src="photo"
              max-width="100%"
              max-height="100%"
              width="auto"
              height="auto"
              contain
              eager
              @click="isModalOpened = true"
              @load="handleImageLoad"
            />
          </v-carousel-item>
        </v-carousel>
      </template>
      <template v-else>
        <v-carousel
          key="carousel-desktop"
          ref="carousel"
          v-model="activePhotoIndex"
          :show-arrows="shouldShowControls && !$vuetify.breakpoint.mobile"
          height="400"
          hide-delimiters
          @change="handleActivePhotoIndexChange"
        >
          <v-carousel-item
            v-for="({ url: photo }) in photos"
            :transition="false"
            :key="photo"
            eager
          >
            <v-img
              ref="images"
              :src="photo"
              max-width="100%"
              max-height="100%"
              width="auto"
              height="auto"
              contain
              eager
              @click="isModalOpened = true"
            />
          </v-carousel-item>
        </v-carousel>
      </template>
    </div>

    <div
      v-if="shouldShowControls"
      class="thumbnails mt-2"
    >
      <v-img
        ref="thumbnails"
        eager
        v-for="({ url: photo }, index) in photos"
        :key="`thumbnail${photo}`"
        :src="photo"
        class="thumbnail ma-1"
        width="145"
        max-width="145"
        :height="index === activePhotoIndex ? 120 : 85"
        @click="() => slideChange(index)"
      />
    </div>

    <v-dialog
      v-model="isModalOpened"
      @keydown.esc="isModalOpened = false"
    >
      <v-btn
        class="px-4 black download-image-button text-center"
        height="38"
        absolute
        right
        small
        @click="() => handlePhotoDownload(photos[activePhotoIndex])"
      >
        <v-icon
          left
          color="white"
          class="ma-0"
        >
          mdi-download
        </v-icon>
      </v-btn>

      <v-btn
        class="modal-close black"
        width="40"
        height="38"
        absolute
        small
        @click="isModalOpened = false"
      >
        <v-icon
          color="white"
          :size="26"
        >
          mdi-close
        </v-icon>
      </v-btn>

      <v-btn
        v-if="photos.length > 1"
        class="prev"
        fab
        dark
        icon
        color="transparent"
        @click="() => slideChange(prevPhotoIndex)"
      >
        <v-icon
          dark
          color="black"
          size="80"
        >
          mdi-chevron-left
        </v-icon>
      </v-btn>

      <div
        class="content-wrapper"
        @click="isModalOpened = false"
      >
        <img
          :key="photos[activePhotoIndex].url"
          class="fullscreen-image"
          :src="photos[activePhotoIndex].url"
          @click.self.stop="handleImageClick"
        />
      </div>

      <v-btn
        v-if="photos.length > 1"
        class="next"
        fab
        icon
        dark
        color="transparent"
        @click="() => slideChange(nextPhotoIndex)"
      >
        <v-icon
          color="black"
          dark
          size="80"
        >
          mdi-chevron-right
        </v-icon>
      </v-btn>
    </v-dialog>
  </div>
</template>

<script>
import saveAs from 'file-saver';
import getRandomInt from '@/utils/helpers/getRandomInt';

export default {
  name: 'PhotoGallery',
  props: {
    photos: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      isMounted: false,
      isPageResizing: false,
      activePhotoIndex: 0,
      isModalOpened: false,
    };
  },
  computed: {
    mobileCarouselHeight() {
      if (this.isPageResizing) {
        return 'auto';
      }

      if (this.isMounted) {
        return this.$refs.carousel.$el.offsetHeight;
      }

      return 'auto';
    },
    shouldShowControls() {
      return this.photos.length !== 1;
    },
    lastPhotoIndex() {
      return this.photos.length - 1;
    },
    isActivePhotoLast() {
      return this.activePhotoIndex === this.lastPhotoIndex;
    },
    isActivePhotoFirst() {
      return this.activePhotoIndex === 0;
    },
    prevPhotoIndex() {
      return this.isActivePhotoFirst ? this.lastPhotoIndex : this.activePhotoIndex - 1;
    },
    nextPhotoIndex() {
      return this.isActivePhotoLast ? 0 : this.activePhotoIndex + 1;
    },
  },
  methods: {
    handlePhotoDownload({ url, filename }) {
      const urlSplittedByDots = url.split('.');
      const format = urlSplittedByDots[urlSplittedByDots.length - 1];

      fetch(`${url}?${getRandomInt(10000)}`, { method: 'GET' })
        .then((res) => res.blob())
        .then((blob) => {
          saveAs(blob, `${filename}.${format}`);
        })
        .catch((err) => {
          // TODO: log error - console.error(err);
          throw err;
        });
    },
    handleImageLoad() {
      if (this.activePhotoIndex === 0) this.resetGalleryHeight();
    },
    handleImageClick({ clientX }) {
      const { innerWidth } = window;
      const middleOfScreen = innerWidth / 2;
      const nextSliderActiveIndex = middleOfScreen >= clientX ? this.prevPhotoIndex : this.nextPhotoIndex;
      this.slideChange(nextSliderActiveIndex);
    },
    resetGalleryHeight() {
      this.handlePageResize();
    },
    handlePageResize() {
      this.isPageResizing = true;
      setTimeout(() => {
        this.isPageResizing = false;
      });
    },
    slideChange(nextIndex) {
      this.$refs.carousel.$emit('change', nextIndex);
    },
    handleActivePhotoIndexChange(index) {
      setTimeout(() => {
        if (!this.$refs.thumbnails) return;
        const thumb = this.$refs.thumbnails[index].$el;
        const { offsetWidth, offsetLeft, offsetParent: parent } = thumb;
        const elRight = offsetWidth + offsetLeft;
        const elLeft = offsetLeft;

        const parentLeft = parent.scrollLeft;
        const parentRight = parent.offsetWidth + parent.scrollLeft;

        if (elRight < parentRight && elLeft > parentLeft) return;

        if (elRight > parentRight) {
          parent.scrollTo({
            behavior: 'smooth',
            left: parentLeft + 150,
          });
        }

        if (elLeft < parentLeft) {
          parent.scrollTo({
            behavior: 'smooth',
            left: elLeft - 150,
          });
        }
      });
    },
  },
  mounted() {
    setTimeout(() => {
      this.isMounted = true;
    });
  },
};
</script>

